
          @import "@/assets/scss/variables.scss";
        

.calendar-wrap-sub {
  max-width: 400px;
  margin: 20px auto;
  font-family: Arial, sans-serif;
  text-align: center;
}

.calendar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  font-weight: bold;
  font-size: 18px;
  span{
    color: #191c21;
  }
}

.calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr); /* 7일 기준 */
  gap: 5px;
}

.calendar-day {
  padding: 10px;
  border-radius: 4px;
  text-align: center;
  font-size: 14px;
}

.calendar-day.header {
  font-weight: bold;
}

.calendar-day.current-date {
  background-color: #E57373;
  color: #FFFFFF;
  font-weight: 500;
  border-radius: 999px;
}

