
          @import "@/assets/scss/variables.scss";
        

.mobile-wrap{
  position: relative;
  overflow: hidden;
  .mobile-content-wrap{
    position: absolute;
    right: 0;
    //background: linear-gradient(to bottom, #FFFBF0, #fff); /* 연한 크림색 계열 */
    background: rgba(255, 255, 255, 0.8); /* 흰색 반투명 */


    height: 100vh;
    overflow-x: hidden;
    overflow-y: scroll;
    padding: 12px 0 24px 0;
    box-shadow: 2px 4px 6px rgba(0, 0, 0, 0.1), 0 1px 3px rgba(0, 0, 0, 0.06);
    max-width: 475px;
    width: 100%;
    .mobile-content {
      margin-bottom: 16px;

      .content-image{
        height: 100%;
        width: 100%;
      }

      .content-empty-image {
        height: 200px;
        background: linear-gradient(
                to bottom,
                rgba(0, 0, 0, 0.2),
                rgba(0, 0, 0, 0.5)
        );
        color: #fff;
        font-size: 20px;
        font-weight: bold;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 8px;
      }
      .content-description {
        margin-top: 8px;
        font-size: 16px;
        color: #333;
        text-align: center;
      }
    }
  }
  // 웹으로 들어온 경우가 아닌경우
  .onlyMobile {
    max-width: 100%;
  }
}

// 스크롤 옵션
.mobile-content-wrap::-webkit-scrollbar {
  width: 0;
  background: transparent;
}

.mobile-content-wrap {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

// 타이핑효과
.main-intro-wrap {
  padding-top: 12px;
  padding-left: 12px;
  height: 100vh;
  background-color: black;
  z-index: 1000;
  opacity: 1;
  transition: opacity 1s ease-in-out, visibility 1s ease-in-out;
  visibility: visible;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main-intro-wrap.hide {
  opacity: 0;
  visibility: hidden;
}

.typing-text {
  font-size: 21px;
  font-weight: bold;
  color: #FFFFFF;
  white-space: nowrap;
  overflow: hidden;
  border-right: 1px solid #FFFFFF;
  animation: blink 0.7s steps(2, start) infinite;
}

@keyframes blink {
  0% {
    border-right-color: #FFFFFF;
  }
  50% {
    border-right-color: transparent;
  }
  100% {
    border-right-color: #FFFFFF;
  }
}

